			id:1,
			TimeString: '2020-20-20'
		}
	},
	computed:{
		// 将简介只保留前面26个字符，用于展开收起
		showIntroduce:function(){
			if(this.isOpen){
				return this.introduce
			} else{
				// 获取简介中的前面的20个字符的文字
				return this.introduce.substr(0,25)
			}
		}
	},
